.loadAppRoot {
    width: 100%;
    height: 100%;
    
    position: absolute;
    top: 0px;
    left: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    z-index: 3001;
    overflow: hidden;

    background: url('/entities/arena/icons/clouds.png') #8c38b356;

    /*    */
    background-position: 0 0;
    background-repeat: repeat;
    animation: animated-sky 60s linear infinite;

    display: flex;
    justify-content: center;
    align-items: center;

    padding: 24px;
    box-sizing: border-box;

    .loadApp {

        width: 100%;
        max-width: 400px;

        .__content {
            animation: on_popup 0.3s alternate;

            width: 100%;
            border: 10px solid #8c38b386;

            background: repeating-linear-gradient(-45deg,
                    #a43fd3b0,
                    #953dbeb0 10px,
                    #8f33b9b0 10px,
                );

            box-shadow: 0px 0px 4px #000000d2;
            padding: 44px 0px;

            border-radius: 10px;
            box-sizing: border-box;

            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;

            position: relative;

            .__text {
                color: white;
                font-size: 20px;
                padding: 32px 0px;
                padding-top: 28px;

                span {
                    font-size: 26px;
                    clip-path: inset(0 3ch 0 0);
                    animation: l 1.5s steps(10) infinite;
                }
            }

            .__load {
                display: inline-block;

                padding-top: 10px;
                margin-top: -10px;

                svg {
                    animation: 4s linear infinite rotate;
                    transform-origin: 50% 50%;
                }

            }


            // .button {
            //     width: 100%;
            //     display: flex;
            //     justify-content: center;

            //     .__break {
            //         font-size: 18px;
            //         padding: 10px 24px;
            //         color: white;
            //         border: 3px dashed #fff;
            //         border-radius: 24px;
            //     }
            // }
        }
    }
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

// @keyframes rotate-loupe {
//     from {
//         transform: rotate(0deg);
//     }

//     to {
//         transform: rotate(-360deg);
//     }
// }

@keyframes l {
    to {
        clip-path: inset(0 -1ch 0 0)
    }
}

@keyframes animated-sky {
    from {
        background-position: 800px 0;
    }

    to {
        background-position: 0 0;
    }
}