.popout {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    z-index: 3001;

    overflow: hidden;

    .__size {
        width: 100%;
        box-sizing: border-box;
        animation: on_popup 0.3s alternate;

        display: flex;
        justify-content: center;

        .main {
            width: 100%;
            max-width: 400px;
        }

    }

    .__full {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
    }


}


@keyframes on_popup {
    0% {
        transform: scale(0);
    }

    80% {
        transform: scale(1.1);
    }

    100% {
        transform: scale(1);
    }
}