.controlProjector {
  cursor: pointer;
  
  position: absolute;
  left: 13%;
  margin-left: -25px;
  bottom: 100px;
  z-index: 101;

  .__circle {
    box-sizing: border-box;

    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;

    .__button {
      transition: 0.2s;

      &:active {
        transform: scale(0.9)
      }
    }
  }
}